import React, { useEffect } from 'react';
import {
  Avatar,
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Chip,
  Container,
  Dialog,
  Divider,
  FormControl,
  FormControlLabel,
  Grid,
  IconButton,
  InputAdornment,
  List,
  ListItem,
  ListItemAvatar,
  ListItemButton,
  ListItemSecondaryAction,
  ListItemText,
  MenuItem,
  OutlinedInput,
  Radio,
  RadioGroup,
  Select,
  Stack,
  Tab,
  TextField,
  Tooltip,
  Typography,
} from '@mui/material';
import { CancelOutlined } from '@mui/icons-material';
import DeleteIcon from '@mui/icons-material/Delete';
import CommentIcon from '@mui/icons-material/Comment';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import { useTheme } from '@mui/styles';
import { useState } from 'react';
import { apiBaseURL } from 'src/config-global';
import axios from 'axios';
import io from 'socket.io-client';
import { toast } from 'react-toastify';
import { TabContext, TabList, TabPanel } from '@mui/lab';
import { collection, firestore, storage } from 'src/Firebase';
import firebase from 'firebase/app';
import { Link } from 'react-router-dom';
import Menu from '@mui/material/Menu';
import { useRef } from 'react';
import { useAuthContext } from 'src/auth/useAuthContext';
import SettingsIcon from '@mui/icons-material/Settings';
// multiple select style
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
function getStyles(name, personName, theme) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const tabs = ['All', 'Open', 'Pending', 'Closed'];
const access = ['itadmin', 'admin'];

const ChatLog = ({ msg }) => {
  const date = new Date(msg.createdOn || Date.now());
  const dateArr = date.toDateString().split(' ');
  const timeArr = date.toLocaleTimeString().split(' ');
  return (
    <Grid container item xs={12} padding={1}>
      <Grid item xs={0.8}>
        <Avatar alt="me" src={msg.dp} sx={{ height: '30px', width: '30px' }} />
      </Grid>
      <Grid item xs={10}>
        <Typography variant="subtitle1">{msg.from}</Typography>
        <Typography variant="body1">{msg.message}</Typography>
      </Grid>
      <Grid item xs={1.2}>
        <Stack justifyContent="center" alignItems="center">
          <Typography variant="caption">{dateArr[1] + ' ' + dateArr[2]}</Typography>
          <Typography variant="caption">
            {timeArr[0].split(':').slice(0, 2).join(':') + ' ' + timeArr[1]}
          </Typography>
        </Stack>
      </Grid>
      {msg.attachments.length !== 0 && (
        <Grid item xs={12} justifyContent="space-between" sx={{ width: '100%' }}>
          {msg.attachments.map((file, index) => {
            return (
              <>
                {/*  <img
                                 key={file.name}
                                 src={typeof file === 'string' ? file : URL.createObjectURL(file)}
                                 alt="file"
                                 style={{ marginRight: '10px', width: '80px', height: '80px', objectFit: 'contain' }}
                             /> */}
                <a
                  href={typeof file === 'string' ? file : URL.createObjectURL(file)}
                  target="_blank"
                  // style={{ marginRight: '10px', width: '80px', height: '80px', objectFit: 'contain' }}
                >
                  {`Attachment ${index + 1}`}
                </a>{' '}
              </>
            );
          })}
        </Grid>
      )}
    </Grid>
  );
};

const Ticket = () => {
  const { user, getDataByRole } = useAuthContext();
  const theme = useTheme();

  const [socket, setSocket] = useState(null);
  const [profileView, setProfileView] = useState(false);
  const [menu, setMenu] = useState(false);
  const [tabTitle, setTabTitle] = useState('All');
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([]);
  const [currentTicket, setCurrentTicket] = useState(null);
  const [ticketType, setTicketType] = useState('myTickets');
  const [tickets, setTickets] = useState([]);
  const [open, setOpen] = React.useState(false);
  const [title, setTitle] = React.useState('');
  const [teamTabValue, setTeamTabValue] = React.useState('1');

  const getAllEmployees = () => {
    const queryRef = firestore.collection(collection).where('company', '==', user.company);
    const temp = [];
    queryRef.get().then((snapshot) => {
      snapshot.forEach((doc) => {
        let data = doc.data();
        if (doc.id !== user.uid) {
          data.uid = doc.id;
          temp.push(data);
        }
      });
      setAllEmployees(temp);
    });
  };

  const handleTeamTabChange = (event, newValue) => {
    event.preventDefault();
    setTeamTabValue(newValue);
    if (newValue === '1') {
      getAllEmployees();
    }
  };

  const [allEmployees, setAllEmployees] = useState([]);
  const handleClickOpen = (e, formTitle) => {
    getAllEmployees();
    setSelectedEmployee('None');
    setSelectedTasks([]);
    setOpen(true);
    setTitle(formTitle);
  };

  const handleClose = () => {
    setOpen(false);
    setDisableSelectedTasks(false);
  };

  const addMessage = (message, attachments, from, dp, email, index) => {
    setMessages((prev) => [...prev, { from, message, attachments, dp, email }]);
    setInput('');
    setAttachments([]);
    const createdOn = new Date().toISOString();
    const chatData = {
      from,
      message,
      attachments,
      email,
      dp,
    };
    if (attachments.length !== 0) return chatData;

    axios.patch(`${apiBaseURL}/tickets/chat/${currentTicket._id}`, { chatData }).then((res) => {
      setTickets((prev) => {
        const ticket = prev[index];
        ticket?.chats?.push({ ...chatData, createdOn });
        prev[index] = ticket;
        return prev;
      });
    });
  };

  const handleQuery = (index, attachments) => async (e) => {
    if (!input) return;
    // console.log(index);
    const to = user.email === currentTicket.email ? currentTicket.assigned : currentTicket.email;
    sendMessage({
      message: input,
      attachments,
      to,
      from: user.displayName,
      email: user.email,
      dp: user.photoURL,
      ticketId: currentTicket._id,
      createdOn: new Date().toISOString(),
    });
    addMessage(input, attachments, user.displayName, user.photoURL, user.email, index);
  };

  const sendMessage = (data) => {
    socket.emit('sendMessage', data);
  };

  const handleTabChange = (e) => {
    setTabTitle(e.target.innerText);
  };

  const [ticketEmployees, setTicketEmployees] = useState(null);
  const getEmployeesToAssign = () => {
    firestore
      .collection(collection)
      .where('teams', 'array-contains-any', ['tickets'])
      .get()
      .then((snapshot) => {
        const temp = [];
        snapshot.forEach((doc) => {
          let data = doc.data();
          if (doc.id !== user.uid) {
            data.uid = doc.id;
            temp.push(data);
          }
        });
        setTicketEmployees(temp);
      });
  };

  useEffect(() => {
    getEmployeesToAssign();
  }, []);

  const assignTickets = async () => {
    const data = { selectedEmployee, selectedTasks };
    // console.log(data)
    const res = await axios.patch(`${apiBaseURL}/tickets`, { formData: data });
    if (res.data === selectedTasks.length) {
      toast.success('Tickets Assigned to ' + selectedEmployee);
      setCurrentTicket((prev) => ({
        ...prev,
        assigned: selectedEmployee,
      }));
      setTickets((prev) => {
        selectedTasks.forEach((ticketId) => {
          tickets.find(({ _id }) => _id === ticketId)['assigned'] = selectedEmployee;
        });
        return prev;
      });
      setSelectedEmployee('None');
      setSelectedTasks([]);
    }
  };

  const [disableSelectedTasks, setDisableSelectedTasks] = useState(false);
  const [selectedEmployee, setSelectedEmployee] = useState('None');
  const [selectedTasks, setSelectedTasks] = useState([]);
  const handleSelectedTasks = (event) => {
    const {
      target: { value },
    } = event;
    setSelectedTasks(value);
  };

  const [statusSelect, setStatusSelect] = useState('open');
  const handleStatusChange = async (status) => {
    const payload = currentTicket;
    const res = await axios.patch(`${apiBaseURL}/tickets?operation=status`, {
      formData: { _id: payload._id, status },
    });
    if (res.data === 1) {
      tickets[payload.index].status = status;
      setStatusSelect(status);
      axios
        .post(`${apiBaseURL}/email/send?service=ticket&event=${status}`, {
          formData: {
            to: payload.email,
            ref: payload,
          },
        })
        .then((res) => {
          console.log(res.data);
        });
    }
  };

  const [privateReply, setPrivateReply] = useState(false);
  const [privateMsg, setPrivateMsg] = useState('');

  const addPrivateReply = async () => {
    const tempTicket = currentTicket;
    // const tempChat = currentChat;
    const formData = {
      ticketId: currentTicket._id,
      // chatId: currentChat._id,
      reply: {
        email: user.email,
        from: user.displayName,
        dp: '',
        message: privateMsg,
      },
    };
    // console.log(formData);
    const res = await axios.patch(`${apiBaseURL}/tickets/pvtreply`, { formData });
    if (res.status === 200) {
      // console.log(tempTicket.index, tempChat.index);
      const temp = tickets[tempTicket.index].replies;
      if (!temp) {
        temp = [];
      }
      temp.push(formData.reply);
      tickets[tempTicket.index]['replies'] = temp;
      if (currentTicket?._id === tempTicket._id)
        setCurrentTicket((prev) => ({ ...tickets[tempTicket.index], index: prev.index }));
    }
    setPrivateMsg('');
  };

  const [anchorEl, setAnchorEl] = useState(null);
  const menuFlag = Boolean(anchorEl);
  const openMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const closeMenu = () => {
    setAnchorEl(null);
  };

  const filesInput = useRef();
  const [attachments, setAttachments] = useState([]);

  const uploadAttachments = (files) => {
    const promises = files.map(
      (file) =>
        new Promise((resolve, reject) => {
          const fileName = new Date().getTime() + file.name;
          const storageRef = storage.ref();
          const uploadTask = storageRef.child(`/Attachments/${fileName}`).put(file);
          uploadTask.on(
            'state_changed',
            (snapshot) => {
              const uploaded = Math.floor((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
              // setProgress(uploaded);
            },
            (error) => {
              console.log(error);
            },
            () => {
              uploadTask.snapshot.ref
                .getDownloadURL()
                .then((url) => {
                  resolve(url);
                })
                .catch((err) => {
                  console.log(err);
                });
            }
          );
        })
    );
    return promises;
  };

  const restrictedRoles = new Set(['admin', 'itadmin']); //to avoid task assignment to these roles
  const [settings, setSettings] = useState(() =>
    user.firestore.settings ? user.firestore.settings : {}
  );
  const saveSettings = () => {
    // console.log(settings);
    firestore
      .collection(collection)
      .doc(user.uid)
      .update({ settings })
      .then(() => {
        toast.success(`Applied settings`);
        user.firestore.settings = settings;
      });
    handleClose();
  };

  useEffect(() => {
    const container = document.getElementById('ticketUIContainer');
    const dec = 60 + 8 * 3 * 2; //header.offsetheight + paddingY
    container.style.height = `${window.innerHeight - dec}px`;
    window.addEventListener('resize', () => {
      container.style.height = `${window.innerHeight - dec}px`;
    });
    return () =>
      window.removeEventListener('resize', () => {
        console.log('window resize listener removed');
      });
  }, []);

  useEffect(() => {
    const searchBar = document.getElementById('searchBar');
    const chatBox = document.getElementById('chatBox');
    if (chatBox.scrollHeight > chatBox.offsetHeight) {
      searchBar.style.position = 'sticky';
    }
    chatBox.scrollTop = chatBox.scrollHeight;
  }, [messages]);

  useEffect(() => {
    if (!currentTicket) return;
    setMessages(currentTicket.chats);
  }, [currentTicket]);

  useEffect(() => {
    if (!user) return;
    setSocket(
      io(apiBaseURL, {
        query: { email: user.email },
      })
    );

    if (ticketType === 'myTickets') {
      axios.get(`${apiBaseURL}/tickets/${user.company}?email=${user.email}`).then((res) => {
        setTickets(res.data);
        const fabricatedParam = { target: { innerText: 'Open' } };
        handleTabChange(fabricatedParam);
      });
    } else {
      // if (user.firestore.role === 'itadmin') {
      if (access.includes(user.firestore.role)) {
        axios.get(`${apiBaseURL}/tickets/${user.company}`).then((res) => {
          setTickets(res.data);
        });
      } else {
        axios.get(`${apiBaseURL}/tickets/${user.company}?assigned=${user.email}`).then((res) => {
          setTickets(res.data);
          const fabricatedParam = { target: { innerText: 'Open' } };
          handleTabChange(fabricatedParam);
        });
      }
    }
  }, [user, ticketType]);

  useEffect(() => {
    if (!socket) return;
    // listen for incoming messages
    // console.log(`subscribed-rec`);
    socket.on('recieveMessage', (data) => {
      const { ticketId } = data;
      // console.log(ticketId);
      const ticket = tickets.find((ticket) => ticket._id === ticketId);
      if (!ticket) return;
      ticket.chats.push(data);
      if (currentTicket?._id === ticketId) {
        setCurrentTicket({
          ...ticket,
        });
      }
    });
    return () => {
      socket.off('recieveMessage');
      // console.log(`unsubscribed-rec`);
    };
  }, [socket, tickets, currentTicket]);

  return (
    <>
      <Dialog
        maxWidth="sm"
        fullWidth
        fullScreen={title === 'Settings'}
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: title === 'Settings' ? null : '75vh',
            overflowY: 'auto',
          },
        }}
      >
        <Container sx={{ mt: 3, mb: 3 }}>
          <Card variant="outlined">
            <CardContent>
              {title !== 'Team' ? (
                <>
                  <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Typography variant="h2">{title}</Typography>
                  </div>
                  <br />
                </>
              ) : null}
              {title === 'Assign Tickets' ? (
                <>
                  <Typography variant="p">Select Employee</Typography>
                  <br />
                  <FormControl fullWidth>
                    <Select
                      fullWidth
                      id="demo-simple-select"
                      value={selectedEmployee}
                      onChange={(e) => {
                        setSelectedEmployee(e.target.value);
                      }}
                    >
                      {ticketEmployees ? (
                        <MenuItem value="None">None</MenuItem>
                      ) : (
                        <MenuItem value="None">No employee found</MenuItem>
                      )}
                      {ticketEmployees &&
                        ticketEmployees.map((emp, index) => {
                          return (
                            <MenuItem key={emp.email} value={emp.email}>
                              {emp.email}
                            </MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>
                  <br />
                  <br />
                  <Typography variant="p">Select Tickets to assign.</Typography>
                  <br />
                  <FormControl disabled={disableSelectedTasks} fullWidth>
                    <Select
                      id="demo-multiple-chip"
                      multiple
                      value={selectedTasks}
                      onChange={handleSelectedTasks}
                      input={<OutlinedInput id="select-multiple-chip" label="Managers" />}
                      renderValue={(selected) => (
                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                          {selected.map((value) => (
                            <Chip
                              key={value}
                              label={tickets.find((ticket) => ticket._id === value).title}
                            />
                          ))}
                        </Box>
                      )}
                      MenuProps={MenuProps}
                    >
                      {tickets.map(({ _id, title, assigned }) => {
                        if (assigned) return;
                        return (
                          <MenuItem key={_id} value={_id} style={getStyles(title, tickets, theme)}>
                            {title}
                          </MenuItem>
                        );
                      })}
                    </Select>
                  </FormControl>
                </>
              ) : title === 'Team' ? (
                <>
                  <Box sx={{ width: '100%', typography: 'body1' }}>
                    <TabContext value={teamTabValue}>
                      <Box sx={{ borderBottom: 2, borderColor: 'divider' }}>
                        <TabList
                          onChange={handleTeamTabChange}
                          textColor="secondary"
                          indicatorColor="secondary"
                          aria-label="lab API tabs example"
                          centered
                        >
                          <Tab label="Team Members" value="1" />
                          <Tab label="Add Members" value="2" />
                        </TabList>
                      </Box>
                      <TabPanel value="1">
                        <List sx={{ p: 0 }}>
                          {allEmployees.map((item, index) => {
                            if (item.teams?.includes('tickets')) {
                              return (
                                <>
                                  <ListItem
                                    id={item.uid}
                                    key={index}
                                    sx={{ p: 0 }}
                                    secondaryAction={
                                      <IconButton
                                        edge="end"
                                        aria-label="delete"
                                        sx={{ borderRadius: '50%' }}
                                        onClick={() => {
                                          firestore
                                            .collection(collection)
                                            .doc(item.uid)
                                            .update({
                                              teams:
                                                firebase.firestore.FieldValue.arrayRemove(
                                                  'tickets'
                                                ),
                                            })
                                            .then((res) => {
                                              const teamMem = document.getElementById(item.uid);
                                              teamMem.style.display = 'none';
                                            });
                                        }}
                                      >
                                        <DeleteIcon />
                                      </IconButton>
                                    }
                                  >
                                    <ListItemAvatar>
                                      <Avatar alt="DP" src={item.photoURL} />
                                    </ListItemAvatar>
                                    <ListItemText
                                      primary={item.displayName}
                                      secondary={item.email}
                                    />
                                  </ListItem>
                                  <Divider />
                                </>
                              );
                            }
                          })}
                        </List>
                      </TabPanel>
                      <TabPanel value="2">
                        <Typography variant="p">Select Employees</Typography>
                        <br />
                        <FormControl fullWidth>
                          <Select
                            id="demo-multiple-chip"
                            multiple
                            value={selectedTasks}
                            onChange={handleSelectedTasks}
                            input={<OutlinedInput id="select-multiple-chip" label="Managers" />}
                            renderValue={(selected) => (
                              <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                {selected.map((value) => (
                                  <Chip key={value} label={value} />
                                ))}
                              </Box>
                            )}
                            MenuProps={MenuProps}
                          >
                            {allEmployees.map((item) => {
                              if (!item.teams || item.teams.includes('tickets')) return;
                              return (
                                <MenuItem
                                  key={item.email}
                                  value={item.email}
                                  style={getStyles(item.email, allEmployees, theme)}
                                >
                                  {item.email}
                                </MenuItem>
                              );
                            })}
                          </Select>
                        </FormControl>
                      </TabPanel>
                    </TabContext>
                  </Box>
                </>
              ) : title === 'Settings' ? (
                <>
                  <br />
                  <Grid container>
                    <Grid item xs={6}>
                      <Typography variant="subtitle1">Appoint a substitute:</Typography>
                      <FormControl fullWidth>
                        <Select
                          fullWidth
                          id="demo-simple-select"
                          value={settings.appointed ? settings.appointed : 'None'}
                          onChange={(e) => {
                            setSettings((prev) => ({
                              ...prev,
                              appointed: e.target.value,
                            }));
                          }}
                        >
                          {allEmployees ? (
                            <MenuItem value="None">None</MenuItem>
                          ) : (
                            <MenuItem value="None">No employee found</MenuItem>
                          )}
                          {allEmployees &&
                            allEmployees.map((emp, index) => {
                              if (restrictedRoles.has(emp.role)) return;
                              return (
                                <MenuItem key={emp.email} value={emp.email}>
                                  {`${emp.email} (${emp.role})`}
                                </MenuItem>
                              );
                            })}
                        </Select>
                      </FormControl>
                    </Grid>
                  </Grid>
                </>
              ) : null}
            </CardContent>
            <CardActions sx={{ float: 'right' }} style={{ padding: '0px 20px 20px' }}>
              <Button
                onClick={() => {
                  if (title === 'Settings')
                    setSettings(user.firestore.settings ? user.firestore.settings : {});
                  handleClose();
                }}
              >
                Close
              </Button>
              {teamTabValue !== '1' || title === 'Assign Tickets' ? (
                <Button
                  size="small"
                  onClick={() => {
                    if (title === 'Team') {
                      const temp = allEmployees.filter((item) =>
                        selectedTasks.includes(item.email)
                      );
                      const promises = [];
                      temp.forEach((item) => {
                        promises.push(
                          firestore
                            .collection(collection)
                            .doc(item.uid)
                            .update({ teams: firebase.firestore.FieldValue.arrayUnion('tickets') })
                        );
                      });
                      Promise.all(promises).then((res) => {
                        console.log('Teams Updated');
                      });
                      setTeamTabValue('1');
                    } else if (title === 'Assign Tickets') {
                      assignTickets();
                    }
                    handleClose();
                  }}
                  variant="contained"
                >
                  Submit
                </Button>
              ) : title === 'Settings' ? (
                <Button size="small" variant="contained" onClick={saveSettings}>
                  Save
                </Button>
              ) : null}
            </CardActions>
          </Card>
        </Container>
      </Dialog>

      <FormControl>
        {/* <FormLabel id="demo-controlled-radio-buttons-group">Gender</FormLabel> */}
        <RadioGroup
          row
          aria-labelledby="demo-controlled-radio-buttons-group"
          name="controlled-radio-buttons-group"
          value={ticketType}
          onChange={(e) => {
            setTicketType(e.target.value);
          }}
        >
          <FormControlLabel value="myTickets" control={<Radio />} label="My Tickets" />
          <FormControlLabel
            value="assigned"
            control={<Radio />}
            label={access.includes(user.firestore.role) ? 'All Tickets' : 'Assigned Tickets'}
          />
        </RadioGroup>
      </FormControl>
      <br />
      <br />

      <div
        id="ticketUIContainer"
        style={{ border: '2px solid #f0f0f0', borderRadius: '25px', overflowY: 'hidden' }}
      >
        <Grid container sx={{ height: '100%' }}>
          {false && (
            <>
              <Grid item xs={3} sx={{ height: '100%' }}>
                <Typography variant="subtitle1" sx={{ mt: 2, ml: 2, alignItems: 'center' }}>
                  Menu
                </Typography>
                <Divider />
                <List sx={{ p: 0 }}>
                  <ListItemButton
                    onClick={(e) => {
                      handleClickOpen(e, 'Team');
                    }}
                  >
                    <Typography
                      variant="body1"
                      sx={{ width: '100%' }}
                      onMouseOver={(e) => {
                        e.target.style.color = 'blue';
                      }}
                      onMouseLeave={(e) => {
                        e.target.style.color = '';
                      }}
                    >
                      Team
                    </Typography>
                  </ListItemButton>
                  <ListItemButton
                    onClick={(e) => {
                      getEmployeesToAssign();
                      handleClickOpen(e, 'Assign Tickets');
                      setSelectedTasks([]);
                    }}
                  >
                    <Typography
                      variant="body1"
                      sx={{ width: '100%' }}
                      onMouseOver={(e) => {
                        e.target.style.color = 'blue';
                      }}
                      onMouseLeave={(e) => {
                        e.target.style.color = '';
                      }}
                    >
                      Assign Tickets
                    </Typography>
                  </ListItemButton>
                </List>
              </Grid>
              <Divider orientation="vertical" flexItem />
            </>
          )}
          <Grid
            container
            item
            xs={profileView || privateReply || menu ? 3 : 3.5}
            spacing={2}
            sx={{ height: '100%' }}
          >
            <Grid item xs={12} sx={{ height: '100%' }}>
              <Stack
                direction="row"
                justifyContent="space-around"
                alignItems="center"
                sx={{ paddingX: '7px', paddingY: '5px' }}
              >
                {/* <Typography variant="body1">Body1</Typography>
                                <Typography variant="body2">Body2</Typography>
                                <Typography variant="subtitle1">Subtitle1</Typography>
                                <Typography variant="subtitle2">Subtitle2</Typography>
                                <Typography variant="caption">Caption</Typography>
                                <Typography variant="overline">Overline</Typography>
                                <Typography variant="button">Button</Typography> */}
                                {access.includes(user?.firestore.role) && (
                                    <>
                                        <IconButton
                                            onClick={(e) => {
                                                // setMenu((prev) => !prev);
                                                openMenu(e);
                                            }}
                                            // sx={{ height: '16px', width: '10px', borderRadius: '20px', ml: '2px', mr: '4px' }}
                                        >
                                            <SettingsIcon />
                                        </IconButton>
                                        <Menu
                                            id="basic-menu"
                                            anchorEl={anchorEl}
                                            open={menuFlag}
                                            onClose={closeMenu}
                                            MenuListProps={{
                                                'aria-labelledby': 'basic-button'
                                            }}
                                        >
                                            <MenuItem
                                                onClick={(e) => {
                                                    closeMenu();
                                                    handleClickOpen(e, 'Team');
                                                }}
                                            >
                                                Team
                                            </MenuItem>
                                            <MenuItem
                                                onClick={(e) => {
                                                    closeMenu();
                                                    getEmployeesToAssign();
                                                    handleClickOpen(e, 'Assign Tickets');
                                                    setSelectedTasks([]);
                                                }}
                                            >
                                                Assign Tickets
                                            </MenuItem>
                                            <MenuItem
                                                onClick={(e) => {
                                                    closeMenu();
                                                    handleClickOpen(e, 'Settings');
                                                }}
                                            >
                                                Settings
                                            </MenuItem>
                                        </Menu>
                                    </>
                                )}
                                {tabs.map((tab) => (
                                    <Typography
                                        width="85%"
                                        textAlign="center"
                                        display={user?.firestore.role !== 'itadmin' && tab === 'All' ? 'none' : ''}
                                        variant="subtitle1"
                                        name={tab}
                                        style={{
                                            cursor: 'pointer',
                                            color: tabTitle === tab ? 'blue' : '',
                                            backgroundColor: tabTitle === tab ? '#ebecf0' : '',
                                            borderRadius: '50px'
                                        }}
                                        onClick={handleTabChange}
                                    >
                                        {tab}
                                    </Typography>
                                ))}
                            </Stack>
                            <Divider />
                            <List sx={{ backgroundColor: '#f5f6fa', py: 0, height: '98%', overflowY: 'auto' }}>
                                {tickets.map((ticket, index) => {
                                    if (tabTitle !== 'All' && ticket.status !== tabTitle.toLowerCase()) return;
                                    const dateArr = new Date(ticket.createdOn).toDateString().split(' ');
                                    // const dateArr = ticket.createdOn.toDateString().split(' ');
                                    return (
                                        <>
                                            <ListItemButton
                                                key={ticket._id}
                                                // selected={ticket._id === currentTicket?._id}
                                                sx={{
                                                    px: 1,
                                                    py: 1,
                                                    backgroundColor: ticket._id === currentTicket?._id ? '#b2ccfe' : 'transparent'
                                                }}
                                                onClick={() => {
                                                    if (ticket._id === currentTicket?._id) return;
                                                    const searchBar = document.getElementById('searchBar');
                                                    searchBar.style.position = 'absolute';
                                                    searchBar.style.bottom = '0px';
                                                    setPrivateReply(false);
                                                    setCurrentTicket({ ...ticket, index });
                                                    setStatusSelect(ticket.status);
                                                }}
                                            >
                                                <Grid container>
                                                    <Grid container item xs={2} justifyContent="center" sx={{ overflowX: 'clip' }}>
                                                        <Avatar
                                                            alt="user pic"
                                                            src={ticket.photoURL === user?.photoURL ? ticket.assigned : ticket.photoURL}
                                                        />
                                                    </Grid>
                                                    <Grid item xs={10}>
                                                        <Stack direction="row" justifyContent="space-between" width="100%">
                                                            <>
                                                                {user?.firestore.role === 'itadmin' ? (
                                                                    <div style={{ width: '75%' }}>
                                                                        <Typography
                                                                            variant="caption"
                                                                            noWrap
                                                                            onClick={() => {
                                                                                setMenu(false);
                                                                                setProfileView(true);
                                                                            }}
                                                                            onMouseOver={(e) => {
                                                                                e.target.style.color = 'blue';
                                                                            }}
                                                                            onMouseLeave={(e) => {
                                                                                e.target.style.color = '';
                                                                            }}
                                                                        >
                                                                            {ticket.name}
                                                                        </Typography>
                                                                    </div>
                                                                ) : (
                                                                    <Typography variant="caption" style={{ width: '75%' }} noWrap>
                                                                        #{ticket._id}
                                                                    </Typography>
                                                                )}
                                                            </>
                                                            <Typography variant="overline" style={{ width: '25%', textAlign: 'right' }}>
                                                                {dateArr[1] + ' ' + dateArr[2]}
                                                            </Typography>
                                                        </Stack>
                                                        <Typography width="100%" noWrap variant="subtitle1">
                                                            {ticket.title}
                                                        </Typography>
                                                        <Typography width="100%" noWrap variant="subtitle2">
                                                            {ticket.desc}
                                                        </Typography>
                                                    </Grid>
                                                </Grid>
                                            </ListItemButton>
                                            {index !== tickets.length - 1 && <Divider />}
                                        </>
                                    );
                                })}
                            </List>
                        </Grid>
                    </Grid>
                    <Divider orientation="vertical" flexItem />
                    <Grid id="chatContainer" container item xs={profileView || privateReply || menu ? 5.96 : 8.46} sx={{ height: '100%' }}>
                        <Grid id="chatHeader" item xs={12}>
                            <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ padding: '5px' }}>
                                <div style={{ marginLeft: '16px' }}>
                                    {currentTicket ? (
                                        <Stack direction="row">
                                            <Link
                                                onClick={() => {
                                                    if (!currentTicket || !access.includes(user?.firestore.role)) return;
                                                    setPrivateReply(true);
                                                    // setCurrentChat((prev) => {
                                                    //     let temp = [];
                                                    //     currentTicket?.replies?.map((chat) => {
                                                    //         if (chat.pvtReplies) {
                                                    //             chat.pvtReplies.map((reply) => {
                                                    //                 temp.push({ ...reply, chatId: chat._id });
                                                    //             });
                                                    //         }
                                                    //     });
                                                    //     temp.sort((a, b) => new Date(b.createdOn) - new Date(a.createdOn));
                                                    //     return { pvtReplies: temp };
                                                    // });
                                                }}
                                            >
                                                <Tooltip title="View all private replies">
                                                    <Typography variant="h4">{currentTicket?.title || 'Select a ticket'}</Typography>
                                                </Tooltip>
                                            </Link>
                                            {currentTicket?.assigned && (
                                                <Tooltip
                                                title={
                                                  access.includes(user?.firestore.role)
                                                    ? 'View all private replies'
                                                    : 'Ticket Title'
                                                }
                                              >
                                                    <img
                                                        style={{ marginLeft: '8px', height: '25px', width: '25px', float: 'right' }}
                                                        src="https://img.icons8.com/color/25/null/checked--v1.png"
                                                    />
                                                </Tooltip>
                                            )}
                                        </Stack>
                                    ) : (
                                        <Typography variant="h4">{currentTicket?.title || 'Select a ticket'}</Typography>
                                    )}
                                    <Typography variant="body1">{currentTicket?.desc}</Typography>
                                </div>
                                {access.includes(user?.firestore.role) && (
                                    <Stack direction="row" spacing={1} alignItems="center">
                                        {currentTicket?.assigned === null && (
                                            <Button
                                                size="small"
                                                onClick={(e) => {
                                                    handleClickOpen(e, 'Assign Tickets');
                                                    setDisableSelectedTasks(true);
                                                    setSelectedTasks([currentTicket._id]);
                                                }}
                                            >
                                                Assign
                                            </Button>
                                        )}
                                        {currentTicket && (
                                            <FormControl size="small">
                                                <Select
                                                    id="demo-simple-select"
                                                    value={statusSelect}
                                                    onChange={(e) => {
                                                        handleStatusChange(e.target.value);
                                                    }}
                                                >
                                                    <MenuItem value="open">Open</MenuItem>
                                                    <MenuItem value="pending">Pending</MenuItem>
                                                    <MenuItem value="closed">Closed</MenuItem>
                                                </Select>
                                            </FormControl>
                                        )}
                                    </Stack>
                                )}
                            </Stack>
                            <Divider />
                        </Grid>
                        <Grid item xs={12} sx={{ height: '91%', paddingY: '4px' }}>
                            <div id="chatBox" style={{ position: 'relative', overflowY: 'auto', height: '100%' }}>
                                {/* <Grid container rowSpacing={2}>
                                    {messages?.map((message, index) => {
                                        if (message.email === user.email) message.from = 'You';
                                        return <ChatLog id={`chatLog-${index}`} key={index} msg={message} />;
                                    })}
                                </Grid> */}
                <List sx={{ p: 0 }}>
                  {messages?.map((message, index) => {
                    if (message.email === user.email) message.from = 'You';
                    return (
                      <>
                        <ListItemButton
                          id={`chatLog-${message._id}`}
                          sx={{
                            p: 0,
                            cursor: 'default',
                            // backgroundColor: message.from === 'You' ? '#f0f0f0' : null
                          }}
                          // selected={message._id === currentChat?._id}
                        >
                          <ChatLog key={index} msg={message} />
                          {/* <ListItemSecondaryAction sx={{ mr: '10%' }}>
                                                        <IconButton
                                                            sx={{ borderRadius: '50%' }}
                                                            onClick={() => {
                                                                setCurrentChat({ ...message, index });
                                                                setPrivateReply(true);
                                                            }}
                                                        >
                                                            <CommentIcon />
                                                        </IconButton>
                                                    </ListItemSecondaryAction> */}
                                                </ListItemButton>
                                                <Divider />
                                            </>
                                        );
                                    })}
                                </List>
                                <div
                                    id="searchBar"
                                    style={{
                                        position: 'absolute',
                                        bottom: '0px',
                                        width: '100%',
                                        backgroundColor: '#ffffff'
                                    }}
                                >
                                    {attachments.length !== 0 && (
                                        <Grid item xs={12} justifyContent="space-evenly">
                                            {Object.values(attachments).map((file) => {
                                                return (
                                                    // <img
                                                    //     key={file.name}
                                                    //     src={typeof file === 'string' ? file : URL.createObjectURL(file)}
                                                    //     alt="file"
                                                    //     style={{ marginRight: '10px', width: '80px', height: '80px', objectFit: 'contain' }}
                                                    // />
                                                    <Typography variant="caption">{file.name}</Typography>
                                                );
                                            })}
                                        </Grid>
                                    )}
                                    <Stack direction="row" spacing={2} justifyContent="center" sx={{ width: '100%', paddingY: 1 }}>
                                        <TextField
                                            placeholder="Type your query here ..."
                                            sx={{ width: '80%' }}
                                            value={input}
                                            InputProps={{
                                                endAdornment: (
                                                    <InputAdornment position="end">
                                                        <IconButton
                                                            sx={{ borderRadius: '50%' }}
                                                            aria-label="toggle password visibility"
                                                            onClick={(e) => filesInput.current.click()}
                                                            // onMouseDown={handleMouseDownPassword}
                                                            edge="end"
                                                        >
                                                            <input
                                                                ref={filesInput}
                                                                type="file"
                                                                multiple
                                                                hidden
                                                                onChange={(e) => setAttachments(e.target.files)}
                                                            />
                                                            <AttachFileIcon />
                                                        </IconButton>
                                                    </InputAdornment>
                                                )
                                            }}
                                            onChange={(e) => setInput(e.target.value)}
                                        />
                                        <Button
                                            disabled={!currentTicket || (user.firestore.role !== 'itadmin' && !currentTicket?.assigned)}
                                            variant="contained"
                                            onClick={(e) => {
                                                if (attachments.length) {
                                                    // console.log(Object.values(attachments))
                                                    const tempTicket = currentTicket;
                                                    const chatData = addMessage(
                                                        input,
                                                        Object.values(attachments),
                                                        user.displayName,
                                                        user.photoURL,
                                                        user.email,
                                                        currentTicket?.index
                                                    );

                          const promises = uploadAttachments(Object.values(attachments));
                          Promise.all(promises).then((urls) => {
                            // handleQuery(currentTicket?.index, urls)()
                            const to =
                              user.email === tempTicket.email
                                ? tempTicket.assigned
                                : tempTicket.email;
                            sendMessage({
                              message: input,
                              attachments: urls,
                              to,
                              from: user.displayName,
                              email: user.email,
                              dp: user.photoURL,
                              ticketId: tempTicket._id,
                              createdOn: new Date().toISOString(),
                            });
                            chatData.attachments = urls;
                            axios
                              .patch(`${apiBaseURL}/tickets/chat/${tempTicket._id}`, { chatData })
                              .then((res) => {
                                setTickets((prev) => {
                                  const ticket = prev[tempTicket?.index];
                                  ticket?.chats?.push({
                                    ...chatData,
                                    createdOn: new Date().toISOString(),
                                  });
                                  prev[tempTicket?.index] = ticket;
                                  return prev;
                                });
                              });
                          });
                        } else {
                          handleQuery(currentTicket?.index, [])();
                        }
                      }}
                    >
                      Send
                    </Button>
                  </Stack>
                </div>
              </div>
            </Grid>
          </Grid>
          {profileView && (
            <>
              <Divider orientation="vertical" flexItem />
              <Grid container item xs={3} sx={{ height: '100%' }}>
                <Grid item xs={12}>
                  <IconButton
                    sx={{ float: 'right', borderRadius: '50%' }}
                    onClick={() => setProfileView(false)}
                  >
                    <CancelOutlined />
                  </IconButton>
                  {currentTicket && (
                    <center style={{ padding: '40px 0px' }}>
                      <Avatar
                        alt="profile user"
                        src={currentTicket.photoURL}
                        sx={{ width: 75, height: 75 }}
                      />
                      <br />
                      <Typography variant="h3">{currentTicket.name}</Typography>
                      {/* <Typography variant="caption">Customer</Typography> */}
                    </center>
                  )}
                  <Divider />
                </Grid>
              </Grid>
            </>
          )}
          {privateReply && (
            <>
              <Divider orientation="vertical" flexItem />
              <Grid container item xs={3} sx={{ px: 1, height: '100%' }}>
                <Grid item xs={12} sx={{ height: '28%' }}>
                  <Stack direction="row" justifyContent="space-between" alignItems="center">
                    <Typography variant="caption">All private replies</Typography>
                    <IconButton
                      sx={{ borderRadius: '50%' }}
                      onClick={() => {
                        // setCurrentChat(null);
                        setPrivateReply(false);
                      }}
                    >
                      <CancelOutlined />
                    </IconButton>
                  </Stack>
                  {currentTicket && (
                    <>
                      <TextField
                        // disabled={!currentChat?._id}
                        fullWidth
                        multiline
                        rows={3}
                        value={privateMsg}
                        onChange={(e) => setPrivateMsg(e.target.value)}
                      />
                      <Button
                        disabled={!privateMsg}
                        size="small"
                        sx={{ float: 'right', mt: 1 }}
                        variant="contained"
                        onClick={addPrivateReply}
                      >
                        Add
                      </Button>
                    </>
                  )}
                </Grid>
                <Grid item xs={12} sx={{ height: '72%' }}>
                  <List dense={true} sx={{ p: 0, height: '100%', overflowY: 'auto' }}>
                    {currentTicket?.replies?.map((msg) => {
                      if (msg.email !== user.email) return;
                      const date = new Date(msg.createdOn || Date.now());
                      const dateArr = date.toDateString().split(' ');
                      const timeArr = date.toLocaleTimeString().split(' ');
                      return (
                        <>
                          {/* <ListItemAvatar>
                                                            <Avatar src="img" alt="sender dp" />
                                                        </ListItemAvatar> */}
                          {/* <ListItemText primary={msg.from} secondary={msg.message} /> */}
                          <ListItem sx={{ p: 0 }}>
                            <div style={{ width: '100%' }}>
                              <Typography variant="body1">{msg.message}</Typography>
                              <Stack
                                sx={{ width: '100%' }}
                                direction="row"
                                justifyContent="flex-end"
                                alignItems="center"
                              >
                                <Typography variant="caption" sx={{ mr: 1 }}>
                                  {dateArr[1] + ' ' + dateArr[2]}
                                </Typography>
                                <Typography variant="caption">
                                  {timeArr[0].split(':').slice(0, 2).join(':') + ' ' + timeArr[1]}
                                </Typography>
                              </Stack>
                            </div>
                          </ListItem>
                          {/* <ListItemSecondaryAction>
                                                            
                                                        </ListItemSecondaryAction> */}
                          <Divider />
                        </>
                      );
                    })}
                  </List>
                </Grid>
              </Grid>
            </>
          )}
        </Grid>
      </div>
    </>
  );
};

export default Ticket;
